body {
    font-family: 'Cormorant Garamond', serif;
    color: #fff;
    background: #111111;
    max-width: 1920px;
    position: relative;
    margin: 0 auto; }
h1 {
    font-size: 76px;
    font-weight: 700;
    line-height: 86.4px; }
h2 {
    font-size: 48px;
    font-weight: 700;
    line-height: 57.6px; }
h3, form {
    font-size: 24px;
    font-weight: 700;
    line-height: 28.8px; }
p, a, span, li {
    font-size: 18px;
    font-weight: 400;
    line-height: 23.4px; }
a, li , ul {
    text-decoration: none;
    color: #fff;
    list-style-type: none; }
button {
    font-family: 'Cormorant Garamond', serif;
    background: none;
    border: none; }
.absolute {
    position: absolute; }
.container {
    width: 100%;
    padding: 0 312px; }


header {
    position: absolute;
    z-index: 10;
    left: 0;
    top: 30.6px;
    width: 100%; }

.header-wrap {
    max-width: 1296px;
    margin: 0 auto; }
.header-wrap__ul {

    display: flex;
    justify-content: left;

    li {
        display: inline-block;
        min-height: fit-content;
        font-weight: 600;
        font-size: 17px;
        line-height: 24.4px;
        margin: auto 0; }
    li:first-child {
        margin-right: 86px; }
    li:last-child {
        margin-left: 83px; } }

.textLi {
    padding: 0 40px; }
.textLi a:hover {
    cursor: pointer;
    border-bottom: solid 1px #CDAA7D;
    color: #CDAA7D; }
.textLi-last {
    margin-right: 0; }

.omega {
    // padding: 0 80px
    cursor: default;
    padding: 0 80px; }


.header-wrap-instagram {
    display: block;
    box-sizing: border-box;
    position: relative;
    max-width: fit-content; }

.header-wrap-instagram img {
    margin-bottom: -4px; }
#hwimg1 {
    top: 50%;
    margin-top: -8px;
    left: 50%;
    margin-left: -7px; }
#hwimg2 {
    top: 3px;
    left: 17px; }


.header-wrap-button {
    display: block;
    width: 40px;
    height: 45px;
    border: none;
    position: relative;
    cursor: pointer;
    background: none;
    transform: translateY(-3px);
    margin-bottom: -10px; }
.spanBtn1, .spanBtn2, .spanBtn3 {
    display: block;
    width: 100%;
    height: 1px;
    background: #fff; }
.header-wrap-button:hover .header-span {
    background: #CDAA7D; }

.spanBtn1 {
    margin-left: -10px; }
.spanBtn2 {
    margin-bottom: 10px; }
.spanBtn3 {
    margin-top: 10px; }


.header-line {
    width: 100%;
    height: 1px;
    background: #fff;
    margin-top: 20px; }





main {
    background-image: url(../img/bgc.png);
    background-size: auto 1038px;
    background-repeat: no-repeat;
    background-position: center;
    width: 100%;
    height: 1038px; //проверить
    position: relative; }
.hide-right {
    margin-right: -475px;
    opacity: 0; }
.show-right {
    margin-right: 0;
    opacity: 100; }
.main-right {

    position: absolute;
    max-width: 475px;
    height: 100vh;
    background: #111111;
    top: 0;
    right: 0;
    padding: 42px 80px;
    transition: .3s;
    z-index: 80;
    &__button {
        display: block;
        position: relative;
        width: 28px;
        height: 28px;
        background: #111111;
        border: none;
        margin-bottom: 76px; }
    &__button:hover {
        cursor: pointer; }
    &__button span::after,
    &__button span::before {
        position: absolute;
        top: 50%;
        margin-top: -1px;
        left: 50%;
        margin-left: -14px;
        height: 2px;
        width: 100%;
        background: #fff; }
    &__button:hover span::after,
    &__button:hover span::before {
        background: #CDAA7D; }
    &__button span::before {
        content: '';
        display: block;
        transform: rotate(45deg); }
    &__button span::after {
        content: '';
        display: block;
        transform: rotate(135deg); }
    &__button-modal {
        font-size: 24px;
        display: block;
        color: #fff;
        border: solid 1px #ffffff49;
        padding: 10px 30px;
        border-radius: 50px;
        margin: 0 auto;
        transition: .4s;
        margin-bottom: 20px; }
    &__button-modal:hover {
        border-color: #CDAA7D;
        color: #CDAA7D;
        padding: 10px 60px; }
    &__p {
        margin-top: 50px;
        margin-bottom: 80px; }
    &__contacts {
        margin-bottom: 60px; }
    &__contacts h3,
    &__workMode h3 {
        margin-bottom: 25px; } }

.top-elem {
    padding-bottom: 15px; }

.activ {
    margin-right: 0; }

.main-wrap {
    margin: 0 auto;
    padding: 360px 0px 0px 0px;
    max-width: 503px;
    height: 100%;
    &__tittle {
        margin-bottom: 290px;
        text-align: center; }
    &__interactive {
        display: flex;
        justify-content: space-between;
        margin: 0 auto;
        max-width: fit-content; }
    &__interactive-img {
        display: block;
        position: relative; }
    &__interactive-img img:last-child {
        position: absolute;
        top: 8px;
        left: 8px; }
    &__interactive p {
        display: block;
        font-size: 18px;
        font-weight: 700;
        line-height: 25.2px;
        padding-left: 20px; } }
//paragraph
.paragraph {
    margin-top: 120px;
    margin-bottom: 35px;
    height: 395px;
    &__img {
        display: block;
        width: fit-content;
        margin: 0 auto;
        margin-bottom: 60px; }
    &__text {
        max-width: 856px;
        margin: 0 auto;
        text-align: center; }
    &__text p {
        font-weight: 700;
        font-size: 27px;
        line-height: 35.1px;
        font-family: 'Cormorant Garamond', serif; }
    &__text p:first-child {
        padding-bottom: 20px; } }
//main menu

.main-menu {
    text-align: left;
    margin-bottom: 162px; }
.main-menu__wrap {
    padding: 0 299px;
    text-align: center; }
.main-menu-card {
    box-sizing: border-box;
    display: inline-block;
    margin-top: 60px;
    padding: 0 11px;
    text-align: left;
    &__img {
        position: relative;
        width: 416px;
        height: 320px; }
    &__img img {
        position: absolute; }
    &__img img:first-child {
        top: 0;
        right: 0; }
    &__img img:last-child {
        bottom: 0;
        left: 0; }
    &__button {
        padding-top: 30px; }
    &__button a {
        font-size: 30px;
        font-weight: 400;
        line-height: 36px;
        text-align: left; }
    &__button a:hover {
        color: #CDAA7D; } }

// brand

.brand {
    margin-bottom: 180px;
    &__wrap {
        max-width: 1079px;
        margin: 0 auto;
        display: flex;
        justify-content: space-between;
        opacity: 50%;
        box-sizing: border-box; } }

// galary
.container-galary {
    padding: 0 10px; }
.galary h2 {
    text-align: center;
    margin-bottom: 80px; }
.galary-nav {
    max-width: 722px;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
    margin-bottom: 55px;
    &__button {
        color: #fff;
        font-size: 20px;
        font-weight: 400;
        line-height: 31.2px;
        box-sizing: border-box;
        padding: 0 5px;
        border-bottom: solid 1px #111111; }
    &__button:hover {
        color: #CDAA7D;
        cursor: pointer;
        border-bottom-color: #CDAA7D; } }
.galary-content {
    width: fit-content;
    margin: 0 auto; }
.grid {
    padding-bottom: 100px;
    display: grid;
    grid-template-columns: 416px 416px 416px;
    grid-row-gap: 24px;
    grid-column-gap: 24px; }

footer {
    margin-bottom: 85px; }
.container-footer {
    padding: 0 312px; }
.footer-wrap {
    padding-top: 80px;
    padding-bottom: 80px;
    border-top: solid 1px #ffffff;
    border-bottom: solid 1px rgba(255, 255, 255, .5);
    display: flex;
    justify-content: space-between;
    &__tittle {
        margin-bottom: 25px; }
    &__instagram-inner {
        display: block;
        position: relative; } }
#footImg1 {
    top: 9px;
    left: 9px; }
#footImg2 {
    top: 5px;
    left: 26px; }


/////masters.html
.masters {
    position: relative;
    background: #111111; }
.masters .container {
    padding: 0 20px; }
.masters-inner {
    width: fit-content;
    margin: 0 auto; }
.masters-tittle {
    display: block;
    text-align: center;
    padding: 180px 0 80px 0; }
.masters-menu__wrap {
    display: grid;
    grid-template-columns: 416px 416px 416px;
    grid-row-gap: 60px;
    grid-column-gap: 24px;
    margin-bottom: 100px; }
.masters-menu-card__img {
    position: relative;
    height: 497px; }
.masters-menu-card__img img {
    position: absolute; }
.masters-menu-card__img img:first-child {
    top: 0;
    right: 0; }
.masters-menu-card__img img:last-child {
    bottom: 0;
    left: 0; }
.masters-menu-card__button {
    padding-top: 30px;
    padding-bottom: 10px; }
.masters-menu-card__button a {
    font-size: 27px;
    font-weight: 700;
    line-height: 35.1px; }
.masters-menu-card p {}

// PRICE.HTML

.price-tittle {
    display: block;
    text-align: center;
    padding: 180px 0 80px 0; }
.price-wrap {
    display: flex;
    justify-content: space-between;
    &__list {
        text-align: left;
        min-width: 526px; } }
.price-wrap-item {
    padding-bottom: 20px;
    padding-top: 40px;
    border-bottom: solid 1px #ffffff; }

.price-wrap-item-inner {
    display: flex;
    justify-content: space-between;
    &__tittle,
    &__price {
        display: block;
        width: fit-content;
        font-size: 27px;
        font-weight: 700;
        line-height: 35.1px;
        padding-bottom: 10px; } }

#price1,
#price2 {
    margin-bottom: 80px; }
#price2 {
   flex-direction: row-reverse; }
#price3 {
    margin-bottom: 100px; }

//////COTACTS HTML
.contacts {
    margin-bottom: 100px;
    &__tittle {
        padding: 180px 0 80px 0;
        text-align: center; } }
.map {
    padding-bottom: 80px; }
.contacts-wrap {
    display: flex;
    justify-content: space-between;
    &__block-one {
        display: flex;
        justify-content: space-between;
        min-width: 590px; }
    &__inner-tittle {
        margin-bottom: 25px; } }

// MODAL
.modal {
    position: fixed;
    width: 100%;
    height: 100vh;
    top: 0;
    left: 0;
    background-color: rgba(0, 0, 0, 0.8);
    z-index: 100;
    transition: .3s;
    text-align: center;
    align-content: center;
    &__body {
        min-height: 100%;
        width: 100%;
        padding: 30px; }


    &__content {
        display: block;
        max-width: 800px;
        margin: 0 auto;
        background: #000000;
        color: #ffffff;
        position: relative;
        padding: 60px 30px; }
    &__close {
        position: absolute;
        top: 5px;
        right: 5px;
        color: #ffffff;
        font-size: 32px;
        transition: .4s;
        padding: 15px; }
    &__close:hover {
        color: #CDAA7D;
        transform: rotate(360deg); }

    &__tittle {
        display: block;
        text-align: center;
        font-size: 40px;
        margin: 40px auto; }
    &__text input {
        width: 100%;
        background: none;
        border: none;
        border-bottom: solid 1px #ffffff52;
        padding: 10px 4px;
        margin-bottom: 40px;
        color: #CDAA7D;
        font-size: 24px; }
    &__text input::placeholder {
        color: #ffffff8f;
        padding: 0 4px;
        font-size: 16px;
        font-weight: 400;
        line-height: 23.4px; }
    &__button {
        font-size: 24px;
        display: block;
        color: #fff;
        border: solid 1px #ffffff49;
        padding: 10px 30px;
        border-radius: 50px;
        margin: 0 auto;
        transition: .4s; }
    &__button:hover {
        border-color: #CDAA7D;
        color: #CDAA7D;
        padding: 10px 60px; }
    &__hide {
        opacity: 0; }
    &__show {
        opacity: 100; } }



///all
.hide {
    display: none; }
.show {
    display: block; }


